.wrapper {
  min-height: 510px;
  & > .container {
    padding: 1.5em 1em;
    @media screen and (min-width: 45.000em) {
      padding: 1.5em;
      display: flex;
      justify-content: space-between;
    }
    @media screen and (min-width: 62.000em) {
      padding: 2.5em;
    }
  }
  img,iframe {
    width: 100%;
    object-fit: cover;
    object-position: 0 0;
  }
}


.container {
  position: relative;
  .content--left {
    @media screen and (min-width: 45.000em) {
      width: 67%;
    }
  }
  .content--right {
    @media screen and (min-width: 45.000em) {
      width: 33%;
    }
  }
}
