.article-service {
  padding: 0;
  overflow: auto;
  .base-teaser__title-link {
    color: $colorPink;
    font-size: 15px;
  }
  .teaser-stacklist__excerpt {
    display: block;
  }
  .teaser-stacklist__images,
  .teaser-stacklist__iframes {
    height: auto !important;
    @media screen and (min-width: 45.000em) {
      height: auto !important;
    }
  }
  .teaser-stacklist__thumbnail {
    width: 100%;
  }
  &__content {
    padding: 1.5em 5% 1.5em;
    @media screen and (min-width: 45.000em) {
      padding: 1.5em 10% 3em;
    }
    .article-teaser {
      margin: 2em 0 0;
    }
    .heading--hero {
      margin: 1em 0 2em;
      display: inline-block;
    }
  }
  .comments-posting {
    // position: relative;
    // right: auto;
    // top: auto;
    // @media screen and (min-width: 45.000em) {
    //   width: 525px;
    // }
  }
}
